html{
  scroll-behavior: smooth;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.typing-demo {
  width: 100%;
  animation: typing 2s steps(22), blink .5s step-end infinite alternate;
  white-space: nowrap;
  overflow: hidden;
  border-right: 3px solid;
  font-family: monospace;
 
}
input:focus,textarea:focus{
  outline: none;
 }

@keyframes typing {
  from {
    width: 0
  }
}
    
@keyframes blink {
  50% {
    border-color: transparent
  }
}

@tailwind base;
@tailwind components;
@tailwind utilities;


@layer components{
  .card{
      @apply w-3/6 h-2/4 md:w-60 md:h-60 inline-block snap-center p-2  cursor-pointer md:hover:scale-105 md:ease-in-out md:duration-300;
  }
  .image{
    @apply md:w-full h-full md:h-28 mr-auto ml-auto object-center ;
}
.img{
  @apply w-12 h-12;
}
.ss{
  @apply bg-gray-50 rounded-md px-1 py-1 md:text-sm text-xs;
}
  .paraga{
    @apply whitespace-normal text-xs md:text-sm h-8 ;
}
.btns{
  @apply flex justify-between text-xs md:text-sm whitespace-normal ;
}
.a{
  @apply border py-1 px-1 md:py-0 md:px-0  w-28 text-center mt-2 hover:bg-purple-500 hover:text-white border-purple-500 rounded-md;
}
.header{
  @apply text-center text-2xl font-black md:text-4xl  bg-clip-text text-transparent bg-gradient-to-r from-gray-400 to-black;
}
.txt{
  @apply text-xs text-gray-400;
}
.icons{
  @apply w-8 h-8 hover:fill-gray-500 ;
}
.input{
  @apply border-b-2 p-6 focus:border-purple-500 border-gray-200;
}
.head{
  @apply md:ml-3 md:mr-3 ;
}

}